
import { Vue, Component, Prop } from "vue-property-decorator"

@Component({ name: "ModalCancelarTutoria" })
export default class ModalCancelarTutoria extends Vue {
  @Prop({ required: true })
  value!: boolean

  cancelarTutoria() {
    this.$emit("input", false)
    this.$emit("cancelamento-confirmado")
  }
}
